*{
    padding: 0;
    margin: 0;
    font-family: 'Roboto', sans-serif;
}
body{
    background-color: #f8f9fa;
}
html{
    font-size: 75.5%;
}
.header{
    width: 100%;
    height: 300px;
    /* color: #ced4da; */
    box-shadow: 0 5px 10px  #ced4da ;
    display: grid;
    box-sizing: border-box;
    flex-wrap: wrap;
    /* display: flex;
    align-items: center;
    position: relative;
} */
}
.header ::before{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
.row2{
    position: absolute;
    top: 40px;
    right: 200px;
    text-align: center;
}
.row2 h2{
    font-size: 3rem;
    margin-top: 20px;
    margin-bottom: 15px;
    color: #0C5F9F;
}
.row1 img {
    font-size: 3rem;
    text-align: center;
    margin-left: 165px;
    margin-top: 60px;
}
.search {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-bottom: 22px;
}
.search input{
    outline: 0;
    border: 4;
    width: 350px;
    height: 30px;
    padding: 0 0.5rem;
    font-size: 15px;
    border-radius: 10px;
}
button {
    outline: none;
    border: none;
    height: 55px;
    width: 100px;
    padding: 0 0.5rem;
    background-color: #0C5F9F;
    color: #f8f9fa;
    border-radius: 18px;
}
#container {
    margin: 1rem;
    display: flex;
    flex-direction: column;
  }
  
.items {
    margin-bottom: 1rem;
  }
.container{
    margin: 0;
    width: 100%;
    margin-top: 30px;
    margin: 0 auto;
    display: grid;
    box-sizing: border-box;
    flex-wrap: wrap;

}
.card {
    width: 250px;
    height: 450px;
    background-color: #ced4da;
    border-radius: 1rem;
    padding: 1.5rem;
    margin-bottom: 10px;
    box-shadow: 0 5px 5px #adb5bd;
    transition: .5s;
    text-align: center;
    display: flex;
    justify-content: space-between;
    margin: 3rem;
}
.card img{
    width: 100%;
    height: 200px;
    border-radius: 1rem 1rem 0 0 ;
}
.card .bottom{
    display: flex;
    flex-direction: column;
}
.card .bottom .title{
    font-size: 14px;
    margin-bottom: 12px;

}
.card .bottom .amount{
    /* position: absolute; */
    bottom: 10px;
    left: 10px;
    right: 10px;
    background-color: #f8f9fa;
    color: #343a40;
    font-size: 13px;
    font-weight: bold;
    padding: 0.2rem;

}
.card::hover{
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    background-color: azure;
}
.items {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  
}
.pagination{
  align-items: center;
  justify-content: center;
}

